import request from '@/utils/request'

/**
 * 
 * @param {*} params
 */
export function getSetting(params = {}) {
  return request({
    url: '/setting/info',
    method: 'get',
    params: params
  })
}

/**
 * 
 * @param {*} params
 */
export function updateSetting(params = {}) {
  return request({
    url: '/setting/update',
    method: 'post',
    data: params
  })
}
