<template>
  <b-form @submit.stop.prevent="handleChangePassword()">
    <b-form-group label="" :invalid-feedback="invalidOldPassword">
      <div slot="label">
        <i class="ion ion-ios-medical text-left text-danger"></i> 
      </div>
      <b-input
        type="password"
        v-model="password.old"
        :state="$v.password.old.$dirty ? !$v.password.old.$error : null"
      />
    </b-form-group>
    <b-form-group label="" :invalid-feedback="invalidNewPassword">
      <div slot="label">
        <i class="ion ion-ios-medical text-left text-danger"></i> 
      </div>
      <b-input
        type="password"
        v-model="password.password"
        :state="$v.password.password.$dirty ? !$v.password.password.$error : null"
      />
    </b-form-group>
    <b-form-group label="" :invalid-feedback="invalidRepeatPassword">
      <b-input
        type="password"
        v-model="password.repeatPassword"
        :state="$v.password.repeatPassword.$dirty ? !$v.password.repeatPassword.$error:null"
      />
    </b-form-group>
    <div class="text-right mt-3">
      <b-btn variant="primary" type="submit"></b-btn>
    </div>
  </b-form>
</template>

<script>
import { required, minLength, sameAs } from 'vuelidate/lib/validators'

export default {
  name: 'change-password-form',
  data () {
    return {
      password: {}
    }
  },
  computed: {
    invalidOldPassword () {
      if (!this.$v.password.old.required) {
        return ''
      }
      return undefined
    },
    invalidNewPassword () {
      if (!this.$v.password.password.required) {
        return ''
      }
      if (!this.$v.password.password.minLength) {
        return '8'
      }
      return undefined
    },
    invalidRepeatPassword () {
      if (!this.$v.password.repeatPassword.sameAsPassword) {
        return ''
      }
      return undefined
    }
  },
  validations: {
    password: {
      old: {
        required
      },
      password: {
        required,
        minLength: minLength(8)
      },
      repeatPassword: {
        sameAsPassword: sameAs('password')
      }
    }
  },
  methods: {
    handleChangePassword () {
      this.$v.password.$touch()
      if (!this.$v.password.$anyError) { // 
        this.$nextTick(() => {
          this.$emit('submit', this.password)
        })
      }
    }
  }
}
</script>
