import request from '@/utils/request'

/**
 * 
 * @param {*} params
 */
export function login (params = {}) {
  return request({
    url: '/auth/login_check',
    method: 'post',
    data: params
  })
}

/**
 * 
 * @param {*} params
 */
export function logout (params = {}) {
  return request({
    url: '/auth/logout',
    method: 'post',
    params: params
  })
}
