/* eslint-disable space-before-function-paren */
/* eslint-disable indent */
import { invalid } from './Validator'
import store from '../../../store'

export const formData = {
    title: null,
    author: null,
    organization: null,
    description: null,
    worksType: null,
    content: null,
    photos: [],
    tabs: [],
    remark: null,
    event: null
}

export const formItem = (options) => {
    let newOptions = JSON.parse(JSON.stringify(options))
    return {
        event: {
            label: '',
            placeholder: '',
            invalid: invalid().invalidEvent,
            options: newOptions['events'],
            component: 'Select',
            state: true,
            required: true
        },
        title: {
            label: '',
            placeholder: '',
            state: true,
            invalid: invalid().invalidTitle,
            component: 'Input',
            required: true
        },
        author: {
            label: '',
            placeholder: '',
            state: false,
            component: 'Input',
            required: false
        },
        organization: {
            label: '',
            placeholder: '',
            state: false,
            component: 'Input',
            required: false
        },
        worksType: {
            label: '',
            placeholder: '',
            invalid: invalid().invalidWorksType,
            state: true,
            options: newOptions['worksType'],
            component: 'Select',
            required: true
        },
        description: {
            label: '',
            placeholder: '',
            component: 'Textarea',
            state: false
        },
        content: {
            label: '',
            placeholder: '',
            state: false,
            component: 'Editor',
            required: false
        },
        photos: {
            label: '',
            state: false,
            component: 'UploadImages',
            action: store.getters.setting.domain + '/api/v1/admin/upload/image',
            required: false
        },
        tabs: {
            label: '',
            placeholder: '',
            options: newOptions['tabs'],
            component: 'MultiSelect',
            required: false
        },
        remark: {
            label: '',
            placeholder: '',
            component: 'Textarea',
            state: false
        }
    }
}
