<template>
  <b-form @submit.stop.prevent="handleSaveProfile()">
    <b-form-group>
      <label class="switcher">
        <input type="checkbox" class="switcher-input" v-model="accountData.isEnable" />
        <span class="switcher-indicator">
          <span class="switcher-yes"></span>
          <span class="switcher-no"></span>
        </span>
        <span class="switcher-label"></span>
      </label>
    </b-form-group>
    <b-form-group label="" :invalid-feedback="invalidUsername">
      <div slot="label">
        <i class="ion ion-ios-medical text-left text-danger"></i> 
      </div>
      <b-input
        v-model="accountData.username"
        :state="$v.accountData.username.$dirty ? !$v.accountData.username.$error : null"
      />
    </b-form-group>
    <b-form-group label="">
      <b-input v-model="accountData.email" />
      <!-- <b-input
        v-model="accountData.email"
        :state="$v.accountData.email.$dirty ? !$v.accountData.email.$error : null"
      />-->
    </b-form-group>
    <b-form-group label="" :invalid-feedback="invalidName">
      <div slot="label">
        <i class="ion ion-ios-medical text-left text-danger"></i> 
      </div>
      <b-input
        v-model="accountData.name"
        :state="$v.accountData.name.$dirty ? !$v.accountData.name.$error : null"
      />
    </b-form-group>
    <div class="text-right mt-3">
      <b-btn variant="primary" type="submit"></b-btn>
    </div>
  </b-form>
</template>

<style src="vue-multiselect/dist/vue-multiselect.min.css" ></style>
<style src="@/vendor/libs/vue-multiselect/vue-multiselect.scss" lang="scss"></style>
<script>
import { required } from 'vuelidate/lib/validators'
// import Multiselect from 'vue-multiselect'

export default {
  name: 'profile-form',
  components: {
    // Multiselect
  },
  computed: {
    invalidUsername () {
      if (!this.$v.accountData.username.required) {
        return ''
      }
      return undefined
    },
    invalidName() {
      if (!this.$v.accountData.name.required) {
        return ''
      }
      return undefined
    }
    // invalidEmail () {
    //   if (!this.$v.accountData.email.required) {
    //     return ''
    //   }
    //   if (!this.$v.accountData.email.email) {
    //     return ''
    //   }
    //   return undefined
    // }
  },
  data () {
    return {
      languageOptions: [
        { label: '', value: 'zh_TW' },
        { label: '', value: 'zh_CN' },
        { label: '', value: 'en' },
        { label: '', value: 'pt_PT' }
      ]
    }
  },
  props: {
    accountData: {
      type: Object
    }
  },
  validations: {
    accountData: {
      username: {
        required
      },
      name: {
        required
      }
    }
  },
  methods: {
    handleSaveProfile () {
      this.$v.accountData.$touch()
      if (!this.$v.accountData.$anyError) { // 
        this.$nextTick(() => {
          this.$emit('submit', this.accountData)
        })
      }
    }
  }
}
</script>
