/* eslint-disable indent */
import request from '@/utils/request'

/**
 * 
 * @param {*} params
 */
export function getList(params = {}) {
    return request({
        url: '/review/list',
        method: 'get',
        params: params
    })
}

/**
 * 
 * @param {*} params
 */
export function distributionReviewBy(params = {}) {
    return request({
        url: '/review/distribution_user',
        method: 'post',
        data: params
    })
}

/**
 * 
 * @param {*} params
 */
export function reviewDetail(params = {}) {
    return request({
        url: '/review/review_detail',
        method: 'get',
        params: params
    })
}

/**
 * 
 * @param {*} id
 * @param {*} params
 */
export function updateReview(id, params = {}) {
    return request({
        url: `/review/${id}/update_review`,
        method: 'post',
        data: params
    })
}

