import request from '@/utils/request'

/**
 * 
 * @param {*} params
 */
export function getUserInfo(params = {}) {
  return request({
    url: '/user/info',
    method: 'get',
    params: params
  })
}

/**
 * 
 * @param {*} params
 */
export function getRoles(params) {
  return request({
    url: '/user/roles',
    method: 'get',
    params: params
  })
}

/**
 * 
 * @param {*} params
 */
export function getProfile(params = {}) {
  return request({
    url: '/profile/info',
    method: 'get',
    params: params
  })
}

/**
 * 
 * @param {*} params
 */
export function updateProfile(params = {}) {
  return request({
    url: '/profile/update',
    method: 'post',
    data: params
  })
}

/**
 * 
 * @param {*} params
 */
export function updatePassword(params = {}) {
  return request({
    url: '/profile/change_password',
    method: 'post',
    data: params
  })
}

/**
 * 
 * @param {*} params
 */
export function getUserList(params = {}) {
  return request({
    url: '/user/list',
    method: 'get',
    params: params
  })
}

/**
 * 
 * @param {*} params
 */
export function createUser(params = {}) {
  return request({
    url: '/user/create',
    method: 'post',
    data: params
  })
}

/**
 * 
 * @param {*} params
 */
export function updateUser(id, params = {}) {
  return request({
    url: `/user/${id}/update`,
    method: 'post',
    data: params
  })
}

/**
 * 
 * @param {*} params
 */
export function deleteUser(id, params = {}) {
  return request({
    url: `/user/${id}/delete`,
    method: 'post',
    data: params
  })
}
